  height: auto;
  position: relative; }

.brand-slide__title {
  display: block;
  font-family: "TT Lakes Medium";
  font-weight: 400;
  color: #7E7E82;
  margin: 0;
  margin-bottom: 4px; }

.brand-slide__description {
  font-family: "TT Lakes Regular";
  font-weight: 400;
  font-size: 14px;
  color: black;
  margin: 0;
  margin-bottom: 16px; }

.brand-slide__text-price {
  font-family: "TT Lakes Medium";
  font-weight: 400;
  color: gray;
  font-size: 14px;
  margin: 0;
  margin-bottom: 4px; }

.brand-slide__price {
  font-family: "TT Lakes Regular";
  font-weight: 400;
  color: black;
  font-size: 14px;
  margin: 0;
  margin-bottom: 16px; }

.brand-slide__text-time {
  font-family: "TT Lakes Medium";
  font-weight: 400;
  font-size: 14px;
  color: gray;
  margin: 0;
  margin-bottom: 4px; }

.brand-slide__time {
  font-family: "TT Lakes Regular";
  font-weight: 400;
  font-size: 14px;
  margin: 0;
  color: black; }

.brand-slide__button {
  position: absolute;
  bottom: 16px;
  right: 16px; }

.brand-slide__block--padding-top--more {
  padding: 16px; }

.brand-slide__block--position--relative {
  position: relative; }

.button-order {
  display: inline-block;
  width: 119px;
  height: 32px;
  border-radius: 16px;
  background-color: #FF3E79;
  color: white;
  box-sizing: border-box;
  text-decoration: none;
  padding-top: 4px;
  padding-left: 12px;
  background-image: url(./static/arrow-right.svg);
  background-position: 90% 50%;
  background-repeat: no-repeat; }

.brand-slider__container-header {
  display: none;
  align-items: center;
  margin: 0 40px; }

.brand-slider__container-title {
  font-family: "TT Lakes Medium";
  font-size: 14px;
  color: #7E7E82;
  min-width: 350px; }

.brand-slider__header-price {
  font-family: "TT Lakes Medium";
  font-size: 14px;
  color: #7E7E82;
  min-width: 75px;
  margin-left: auto; }

.brand-slider__header-time {
  font-family: .brand-slider__header-time "TT Lakes Medium";
  font-size: 14px;
  color: #7E7E82;
  min-width: 81px;
  margin-left: auto; }

.brand-slider__empty-block {
  width: 119px;
  margin-left: auto; }

.brand-slider__wrapper--large-window--indent-another {
  padding: 0 16px; }

.brand-slider__about-price {
  font-family: "TT Lakes Regular";
  font-size: 14px;
  color: #1B1C21;
  margin-bottom: 15px;
  line-height: 20px;
  margin-left: 16px;
  letter-spacing: 0.2px;
  max-width: 460px;
  padding-right: 30px; }

.brand-slider__get-order {
  display: inline-block;
  text-decoration: none;
  font-family: "TT Lakes Medium";
  font-weight: 600;
  color: #1B1C21;
  font-size: 16px;
  line-height: 24px;
  margin-left: 16px;
  margin-bottom: 40px; }

.brand-slider__arrow-right {
  position: relative;
  top: 3px;
  font-family: "Glyphter";
  color: #FF3E79;
  font-size: 14px;
  margin-left: 10px; }

@media (min-width: 768px) {
  .brand-slider__container-header--medium-window--visible {
    display: flex; }
  .brand-slider__wrapper--medium-window--other {
    display: block;
    width: auto;
    background-color: #FFFFFF;
    margin: 0 24px; }
  .brand-slider__about-price--medium-window--margin-more {
    margin-left: 24px; }
  .brand-slider__get-order--medium-window--margin-more {
    margin-left: 24px; }
  .brand-slide--medium-window--other {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 64px;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #EAEAEA;
    max-width: 100%;
    padding: 16px 0; }
  .brand-slide__title {
    display: none;
    margin: 0; }
  .brand-slide__time-and-order-container {
    align-items: center; }
  .brand-slide__time {
    margin: 0;
    display: inline-block;
    min-width: 81px;
    margin-left: auto; }
  .brand-slide__description {
    margin: 0;
    display: inline-block;
    min-width: 350px; }
  .brand-slide__text-price {
    display: none;
    margin: 0; }
  .brand-slide__price {
    margin: 0;
    display: inline-block;
    min-width: 75px;
    margin-left: auto; }
  .brand-slide__text-time {
    display: none;
    margin: 0; }
  .brand-slide__time-and-order-container {
    margin: 0; }
  .button-order {
    margin-left: auto; }
    .button-order--medium-window--relative-little {
      position: relative;
      bottom: 0px;
      right: 0px; } }

@media (min-width: 1440px) {
  .brand-slider__wrapper--large-window--margin-more {
    margin: 0px 32px; }
  .brand-slider__container--large-window--margin-more {
    margin: 0 48px; }
  .brand-slider__about-price--large-window--margin-more {
    margin-left: 32px; }
  .brand-slider__get-order--large-window--margin-more {
    margin-left: 32px; } }

.footer {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 16px;
  font-family: "TT Lakes Medium";
  font-size: 14px;
  color: #7E7E82; }
  .footer__text-one {
    margin-bottom: 16px; }
  .footer__text-two {
    margin-bottom: 16px; }
  .footer__text-three {
    margin-bottom: 19px; }

@media (min-width: 768px) {
  .footer__text-one {
    text-align: left; }
  .footer--middle-window--another {
