.modal-window {
  position: absolute;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  padding: 24px 16px;
  background-color: white;
  transform: translateX(100vw);
  transition: transform ease-out 0.3s;
  z-index: 100; }
  .modal-window__button-close {
    box-sizing: border-box;
    margin-bottom: 32px;
    padding-top: 4px; }
  .modal-window__title {
    font-weight: 700;
    font-family: "TT Lakes Bold";
    font-size: 26px; }
  .modal-window__header {
    position: relative;
    left: -16px;
    margin-bottom: 24px; }

.form__name {
  padding: 12px 16px;
  height: 48px;
  width: 100%;
  font-family: "TT Lakes Medium";
  font-size: 16px;
  letter-spacing: -0.02px;
  border-radius: 8px;
  border: 1px solid #EAEAEA;
  margin-bottom: 16px; }

.form__tel {
  padding: 12px 16px;
  height: 48px;
  width: 100%;
  font-family: "TT Lakes Medium";
  font-size: 16px;
  letter-spacing: -0.02px;
  border-radius: 8px;
  border: 1px solid #EAEAEA;
  margin-bottom: 16px; }

.form__email {
  padding: 12px 16px;
  height: 48px;
  width: 100%;
  font-family: "TT Lakes Medium";
  font-size: 16px;
  letter-spacing: -0.02px;
  border-radius: 8px;
  border: 1px solid #EAEAEA;
  margin-bottom: 16px; }

.form__text {
  padding: 12px 16px;
  height: 118px;
  width: 100%;
  font-family: "TT Lakes Medium";
  font-size: 16px;
  letter-spacing: -0.02px;
  border-radius: 8px;
  line-height: 24px;
  border: 1px solid #EAEAEA;
  margin-bottom: 16px; }

.form__link {
  color: #FF3E79;
  text-decoration: none; }

.form__instruction {
  font-family: "TT Lakes Regular";
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  margin-bottom: 19px; }

.form__submit {
  border: none;
  display: block;
  width: 136px;
  padding: 0;
  padding-right: 15px;
  background-position: 95% 50%;
  font-family: "TT Lakes Bold";
  font-size: 12px;
  font-weight: 700;
  margin-left: auto; }

@media (min-width: 768px) {
  .modal-window--middle-window--other {
    width: 440px;
    padding: 40px 32px; }
  .title--middle-window--left-more {
    left: -32px; }
  .title__line--middle-window--margin-more {
    margin-right: 28px; }
  .modal-window__button-close--middle-window--relative-left {
    position: relative;
    left: -100px; } }
