.title {
  font-size: 16px;
  line-height: 24px;
  color: #7E7E82; }

.brand-slider__header {
  margin-top: 0;
  margin-bottom: 16px;
  padding: 16px;
  background: linear-gradient(90deg, #F8F8F8 0%, white 100%); }

/************************************************/
.brand-slide__link {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #FF3E79; }

.brand-slide__link::before {
  content: "";
  width: 7.5px;
  height: 12px;
  left: 15px;
  top: 12px;
  position: absolute;
  background: url(./static/arrow.svg); }

/************************************************/
.brand-slider__container {
  overflow: hidden;
  padding-bottom: 25px; }

.swiper-container {
  width: 100%;
  height: auto; }

.swiper-wrapper {
  padding-left: 16px; }

.swiper-slide {
  width: 240px;
  height: 72px;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center; }

.brand-slide {
  box-sizing: border-box; }

.brand-slider__block {
  text-decoration: none;
  border-radius: 6px;
  padding: 0 16px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA; }

.swiper-pagination {
  position: relative;
  bottom: -15px !important; }

.brand-slider__container .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #B5B6BC; }

.brend-slider__button-container {
  margin-bottom: 32px;
  position: relative; }

.brend-slider__button-icon {
  position: absolute;
  width: 30px;
  height: 30px;
  left: 20px;
  z-index: 3;
  transition: all 0.2s ease-out; }

.brend-slider__button-show {
  font-family: 'TT Lakes Medium';
  color: #1B1C21;
  padding-left: 30px;
  margin-left: 24px;
  background: none;
  border: none;
  font-size: 24px;
  transition: all 0.2s ease-out; }

.brend-slider__button-container {
  display: none;
  transition: all 0.2s ease-out; }

.brend-slider__button-container:hover, .brend-slider__button-container:focus {
  opacity: 0.8; }

@media (min-width: 768px) {
  .brand-slider__wrapper {
    display: grid;
    gap: 16px 24px;
    grid-template-columns: repeat(3, minmax(200px, 1fr)); }
  .brand-slider__header--middle-window--padding-more {
    padding-left: 24px; }
  .brand-slider__wrapper--middle-window--more-padding {
    box-sizing: border-box;
    padding: 0 24px; }
  .brand-slider__container .swiper-slide {
    width: auto; }
  .brend-slider__button-container {
    display: inline-block; } }

@media (min-width: 1440px) {
  .brand-slider__header--large-window--padding-more {
    padding-left: 32px; }
  .brand-slider__wrapper--large-window--more-padding {
    padding: 0 32px;
    gap: 16px 32px; }
  .brand-slider__wrapper--large-window--grid-another {
    grid-template-columns: repeat(4, minmax(200px, 1fr)); }
  .brend-slider__button-show--large-window--padding-more {
    margin-left: 32px; }
  .brend-slider__button-icon--large-window--left-more {
    left: 28px; } }

.hidden {
  display: none; }
